<script>
import $ from 'jquery';

export default {
  name: 'App',
  data(){
    return{
      subMenuOpen: [0,0,0,0,0,0],
      productOpen: [0,0],
      appBar: 0,
      moSubMenuOpen: [0,0,0,0],
      productSubNav1View: 1,
      productSubNav2View: 0,
      productSubNav3View: 0,
      productSubTitle: 0,
      personalModalOpen: 0,
      emailModalOpen: 0,
    }
  },
  components: {
    
  },
  mounted(){
    window.addEventListener('resize', this.resizeFn);

    this.resizeFn();
    
    $(window).scroll(function(){
      if( $(window).scrollTop()>0 ){
        $('header').addClass('scroll');
      }
      else{
        $('header').removeClass('scroll');
      }
    });

  },
  methods: {
    resizeFn(){
      if( window.innerWidth<1024 ){
        $('.mo-header').show();
        $('header').hide();
      }
      else{
        $('.mo-header').hide();
        $('header').show();
      }    
    },
    appBarClick(){
      if( this.appBar == 0 ){
        $('.appbarBtn-line0').addClass('appbarBtn-lineX0');
        $('.appbarBtn-line1').addClass('appbarBtn-lineX1');
        $('.appbarBtn-line2').addClass('appbarBtn-lineX2');
        this.appBar = 1;
      }
      else {
        $('.appbarBtn-line0').removeClass('appbarBtn-lineX0');
        $('.appbarBtn-line1').removeClass('appbarBtn-lineX1');
        $('.appbarBtn-line2').removeClass('appbarBtn-lineX2');        
        this.appBar = 0;
      }
    },
    moSubMenuOpen1Fn(){
      this.moSubMenuOpen[0] =! this.moSubMenuOpen[0];
      this.moSubMenuOpen[1] = 0;
      this.moSubMenuOpen[2] = 0;
      this.moSubMenuOpen[3] = 0;
    },
    moSubMenuOpen2Fn(){
      this.moSubMenuOpen[0] = 0;      
      this.moSubMenuOpen[1] =! this.moSubMenuOpen[1];
      this.moSubMenuOpen[2] = 0;
      this.moSubMenuOpen[3] = 0;
    },
    moSubMenuOpen3Fn(){
      this.moSubMenuOpen[0] = 0;      
      this.moSubMenuOpen[1] = 0;
      this.moSubMenuOpen[2] =! this.moSubMenuOpen[2];
      this.moSubMenuOpen[3] = 0;      
    },
    moSubMenuOpen4Fn(){
      this.moSubMenuOpen[0] = 0;   
      this.moSubMenuOpen[1] = 0;
      this.moSubMenuOpen[2] = 0;
      this.moSubMenuOpen[3] =! this.moSubMenuOpen[3];      
    },
    setProductSubNavView(z){
      if( z==0 ){
        this.productSubNav1View = 1;
        this.productSubNav2View = 0;
        this.productSubNav3View = 0;
        this.productSubTitle = 0;
      }
      if( z==1 ){
        this.productSubNav1View = 0;
        this.productSubNav2View = 1;
        this.productSubNav3View = 0;
        this.productSubTitle = 1;
      }
      if( z==2 ){
        this.productSubNav1View = 0;
        this.productSubNav2View = 0;
        this.productSubNav3View = 1;
        this.productSubTitle = 2;
      }
    }
  }
}
</script>

